/**
 * Mock context value (always returns defaults).
 * In production, this is replaced with the real context from packages/ui.
 */
export interface TemplateFieldContextValue {
  yDoc: null;
  collabUser: null;
  awareness: null;
  availableNodes: any;
  myInterface: any;
  typeDeclarations: string;
  element: any;
  nodeId: string;
  onControlFocus: (context: any) => void;
  onControlBlur: () => void;
  onRecordChange: (change: any) => void;
  onValidationChange: (fieldName: string, error: any) => void;
  parentFieldPath: string | null;
  disabled: boolean;
}

/**
 * Mock hook - returns empty context values.
 * Components work as basic inputs in development mode.
 */
export function useTemplateFieldContext(): TemplateFieldContextValue {
  return {
    yDoc: null,
    collabUser: null,
    awareness: null,
    availableNodes: null,
    myInterface: null,
    typeDeclarations: '',
    element: null,
    nodeId: '',
    onControlFocus: () => {},
    onControlBlur: () => {},
    onRecordChange: () => {},
    onValidationChange: () => {},
    parentFieldPath: null,
    disabled: false,
  };
}

/**
 * Mock hook - always returns false in development mode.
 */
export function useIsInTemplateFieldProvider(): boolean {
  return false;
}

/**
 * Mock hook - returns the fieldName as-is (no parent path in dev mode).
 */
export function useFieldPath(fieldName: string): string {
  return fieldName;
}

/**
 * Mock provider - just renders children without context.
 */
export function TemplateFieldProvider({ children }: { children: React.ReactNode }) {
  return <>{children}</>;
}

/**
 * Mock provider - just renders children without nesting context.
 */
export function NestedFieldProvider({ 
  children 
}: { 
  children: React.ReactNode;
  fieldName: string;
}) {
  return <>{children}</>;
}

